.post__item {
    list-style-type: none;
    padding-left: 15px;
    margin: 10px 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.post__avatar {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 15px;
}

.post__text {
    flex: 1 1 90%;
}

.post__likes {
    display: block;
}