.user {
    display: flex;
    width: 100%;
    column-gap: 10px;
}
  
.user__photo {
    flex:  0 1 20%;
    max-height: 150px;
    border-radius: 5px;
    overflow: hidden;
}

.user__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.user__content {
    flex: 0 1 80%;
    padding: 5px;
}

.user__status {
    display: flex;
    height: 25px;
}

.user__statusInput {
    padding: 0 5px;
    font-size: 15px;
    max-width: 100%;
    width: 100%;
    height: 100%;
}

.user__statusText {
    margin: 0;
    font-size: 1.1rem;
    font-weight: 600;
}